/**
 * Aslan Energy Website - Dynamic i18n Setup  
 * Veritabanından çevirileri yükleyen dinamik i18n sistemi
 */

import { createI18n } from 'vue-i18n'
import ApiService from '../services/api.js'

// Varsayılan çeviriler (API başarısız olursa fallback)
const defaultMessages = {
  tr: {
    nav: {
      home: 'Anasayfa',
      about: 'Hakkımızda',
      services: 'Hizmetlerimiz',
      projects: 'Projelerimiz',
      news: 'Haberler',
      contact: 'İletişim'
    },
    common: {
      loading: 'Yükleniyor...',
      darkMode: 'Karanlık Mod',
      lightMode: 'Aydınlık Mod'
    },
    contact: {
      success: 'Mesajınız başarıyla gönderildi!',
      error: 'Bir hata oluştu. Lütfen tekrar deneyin.'
    }
  },
  en: {
    nav: {
      home: 'Home',
      about: 'About Us',
      services: 'Services',
      projects: 'Projects',
      news: 'News',
      contact: 'Contact'
    },
    common: {
      loading: 'Loading...',
      darkMode: 'Dark Mode',
      lightMode: 'Light Mode'
    },
    contact: {
      success: 'Your message has been sent successfully!',
      error: 'An error occurred. Please try again.'
    }
  }
}

// i18n instance oluştur
const i18n = createI18n({
  legacy: false,
  locale: 'tr',
  fallbackLocale: 'en',
  messages: defaultMessages,
  silentTranslationWarn: true,
  silentFallbackWarn: true
})

// API'den çevirileri yükle
export const loadTranslations = async (locale = 'tr') => {
  try {
    console.log(`Loading translations for ${locale}...`)
    const translations = await ApiService.getTranslations(locale)
    
    if (translations && Object.keys(translations).length > 0) {
      // API'den gelen çevirileri mevcut çevirilerle birleştir
      const currentMessages = i18n.global.messages.value[locale] || {}
      const mergedMessages = { ...currentMessages, ...translations }
      
      i18n.global.setLocaleMessage(locale, mergedMessages)
      console.log(`✓ Translations loaded for ${locale}:`, Object.keys(translations))
      return true
    } else {
      console.warn(`⚠️ No translations received for ${locale}, using defaults`)
      return false
    }
  } catch (error) {
    console.error(`❌ Error loading translations for ${locale}:`, error)
    return false
  }
}

// Dil değişimi fonksiyonu
export const changeLanguage = async (locale) => {
  try {
    // Önce çevirileri yükle
    await loadTranslations(locale)
    
    // Dili değiştir
    i18n.global.locale.value = locale
    
    // Local storage'a kaydet
    localStorage.setItem('language', locale)
    
    console.log(`✓ Language changed to ${locale}`)
    return true
  } catch (error) {
    console.error(`❌ Error changing language to ${locale}:`, error)
    return false
  }
}

// Başlangıçta çevirileri yükle
export const initializeTranslations = async () => {
  try {
    // Kaydedilmiş dili al
    const savedLocale = localStorage.getItem('language') || 'tr'
    
    // Her iki dil için de çevirileri yükle
    await Promise.all([
      loadTranslations('tr'),
      loadTranslations('en')
    ])
    
    // Kaydedilmiş dili ayarla
    i18n.global.locale.value = savedLocale
    
    console.log('✓ i18n initialized with API translations')
    return true
  } catch (error) {
    console.error('❌ Error initializing translations:', error)
    return false
  }
}

export default i18n